.c-container{
    background-color:#fa991c;
    height: 100vh;
}
.contact{
    width: 90%;
    margin-inline: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
}
form{
    margin-top: 7rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}
.input{
    padding: 1rem;
    outline: none;
    width: 20rem;
    border-radius: 0.5rem;
    border: none;
}
.green{
    background-color: aliceblue;
    color: green;
    padding: 1rem;
    border-radius: 0.5rem;
    margin-top: -1rem;
}
.red{
    background-color: aliceblue;
    color: red;
    padding: 1rem;
    border-radius: 0.5rem;
    margin-top: -1rem;
}

@media screen and (max-width: 600px) {
    .contact{
        flex-direction: column;
        margin-bottom: 10rem;
        gap: 0.7rem;
    }
    form{
        margin-top: 1rem;
        gap: 0.8rem;
    }
    .contact>div>h1{
        font-size: 1.2rem;
        color: aliceblue;
    }
}