.about{
    width: 80%;
    margin-inline: auto;
    display: grid;
    gap: 1.5rem;
    grid-template-rows: auto auto auto;
    grid-template-columns: auto auto auto;
}
.card{
    width: 100%;
    margin-top: 3rem;
    background: #1c768f;
    border-radius: 1rem;
    color: #fbf3f2;
    grid-template-rows: subgrid;
    grid-template-columns: subgrid;

}
.card>img{
    width: 100%;
    height: 13rem;
    border-top-right-radius: 1rem;
    border-top-left-radius: 1rem;
}
.card>h2{
    text-align: center;
}
.card>p{
    padding: 1rem;
}
@media screen and (max-width:1000px) {
    .about{
        display: block;
    }
   
}