.navbar{
    background: #032539;
}
.sticky{
    position: sticky;
    top: 0px;
    z-index: 100;
}
.n-container{
     display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin-inline: auto;
}
.n-left{
    color: #FBF3F2;
    font-family: cursive;
    font-weight: bold;
    font-size: 2rem;
}
.n-left>span{
    color: #fa991c;
}
.n-right{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}
.n-right>ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
}
.n-right>ul>*{
    color: #FBF3F2;
    list-style: none;
    font-weight: bold;
    font-size: 1.2rem;
    transition: all 0.5s;
}
.n-right>ul>*:hover{
    cursor: pointer;
    color: #fbf3f2a2;
}
.icon,.iconx {
    color: #FBF3F2;
    font-size: 2rem;
    display: none;

}
.M-ul{
    display: none;
}
.active{
    color: #fbf3f2a2;

}

@media screen and (max-width: 600px){
    .n-container{
        width: 96%;
    }
    .n-right>ul{
        display: none;
    }
    .icon{
        display: block;
    }
    .iconx{
        display: block;
    }
    .M-ul{
        z-index: 10;
        display: block;
        background: #032539;
        position: absolute;
        top: 44px;
        left: 0rem;
        width: 100%;
        height: 100vh;
        align-items: center;
    }
    .M-ul>ul{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        margin-top: 2rem;
    }
    .M-ul>ul>*{
        list-style: none;
        color: #FBF3F2;
        font-size: 1.5rem;
        font-weight: bold;
    }
}