.portfolio{
    width: 90%;
    margin-top: 5rem;
    position: relative;
    margin-bottom: 5rem;
    margin-inline: auto;
}
.row {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4px;
    margin-inline: auto;

}
.column {
    flex: 25%;
    max-width: 25%;
    padding: 0 4px;
}

.column img {
    vertical-align: middle;
    width: 100%;
}
.rotate{
    transform: rotate(10deg);
}
.lastI{
    z-index: 10 !important;
}
.b-locate{
    position: absolute;
    z-index: -1;
}
@media screen and (max-width: 600px) {
    .portfolio{
        margin-top: 5rem;
    }
    .row{
        flex-direction: column;
        width: 100%;
    }
    .column{
        flex: 100%;
        max-width: 100%;
    }
    .column img{
        width: 100%;
    }
    .rotate{
        transform: rotate(0deg);
    }

}