.home{
    
    background: #032539;
   
}
.h-container{
    display: flex;
    width: 96%;
    margin-inline: auto;
}
.h-left{
    flex: 1;
    display: flex;
}
.h-left>img{
    width: 70%;
    margin-top: -4.2rem;
    z-index: 5;
}
.blur{
    filter: blur(78px);
    width: 19rem;
    height: 19rem;
    background: #fa991c;

}
.h-right{
    flex: 1;
    color: #fbf3f2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.h-right>p{
    width: 70%;
    margin-top: -1rem;
}
.button{
    margin-top: 1rem;
    padding: 0.5rem;
    border: none;
    border-radius: 0.25rem;
    background: #fa991c;
    color: #fbf3f2;
    box-shadow: 0px 4px 6px #afc5d1;
    cursor: pointer;
    font-size: 1.2rem;
}
.link{
    margin-top: 7rem;
    display: flex;
    gap: 3rem;
    align-items: center;
    font-size: 2.5rem;
}
.link>a{
    text-decoration: none;
    color: #fa991c;
    opacity: 0.7;
    transition: 0.5s;
}

.link>a:hover{
    opacity: 1;
}

@media screen and (max-width: 600px) {
    .h-container{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .h-left>img{
        width: 100%;
    }
    .h-right{
        background: #1c768f;
        margin-top: -75%;
        z-index: 10;
        border-radius: 1rem;
    }
    .type{
        font-size: 0.7rem;
        margin-bottom: 1rem;
    }
   
    .blur{
        display: none;
    }
        .link {
            margin-top: 2rem;
           
        }
}
@media screen and (max-width: 280px){
    .type{
        font-size: 0.56rem;
    }
}